body {
  margin: 0;
  font-family: Inter, sans-serif;
  background-color: #282D35;
}

.nav {
  display: flex;
  align-items: center;
  background: #21222A;
  height: 90px;
  padding: 30px 25px;
}

.nav-logo {
  width: 30px;
  margin-right: 7px;
}

.nav-logo_text, .nav-title {
  margin: 0;
}

.nav-logo_text {
  margin-right: auto;
  color: #61DAFB;
  font-weight: 700;
  font-size: 26px;
}

.nav-title {
  color: #DEEBF8;
  font-size: 16px;
  font-weight: 600;
  text-align: right;
}

.nav-items {
  list-style: none;
  display: flex;
}

.nav-items > li {
  padding: 10px;
}

main {
  padding: 57px 27px;
  color: #FFFFFF;
  background-image: url("./images/react-icon-large.png");
  background-repeat: no-repeat;
  background-position: right 75%;
}

.main-title {
  margin: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 39.06px;
  letter-spacing: -0.05em;
}

.main-list {
  margin-top: 46px;
  max-width: 400px;
}

.main-list > li {
  line-height: 19px;
  padding-block: 10px;
}

.main-list > li::marker {
  color: #61DAFB;
  font-size: 1.5em;
}